body {
  background: #FFF;
  font-size: 50px;
  font-size: calc(16px + (50 - 16) * ((100vw - 600px) / (1000 - 600)));
  line-height: 1.4;
}

@media (max-width: 600px) {
  body {
    font-size: 16px;
  }
}

@media (min-width: 1000px) {
  body {
    font-size: 50px;
  }
}

@font-face {
  font-family: AgencyFB;
  src: url("../fonts/AgencyFB.woff2") format("woff2"), url("../fonts/AgencyFB.woff") format("woff");
  font-weight: normal;
  font-style: normal;
}

@font-face {
  font-family: OpenSans-R;
  src: url("../fonts/opensans.woff2") format("woff2"), url("../fonts/opensans.woff") format("woff");
  font-weight: normal;
  font-style: normal;
}

@font-face {
  font-family: OpenSans-B;
  src: url("../fonts/opensans-bold.woff2") format("woff2"), url("../fonts/opensans-bold.woff") format("woff");
  font-weight: normal;
  font-style: normal;
}

@font-face {
  font-family: OpenSans-S;
  src: url("../fonts/opensanssemibold.woff2") format("woff2"), url("../fonts/opensanssemibold.woff") format("woff");
  font-weight: normal;
  font-style: normal;
}

@font-face {
  font-family: Raleway-R;
  src: url("../fonts/raleway-regular.woff2") format("woff2"), url("../fonts/raleway-regular.woff") format("woff");
  font-weight: normal;
  font-style: normal;
}

body {
  margin: 0;
  padding: 0;
}

.header {
  width: 100%;
  background-color: #000;
}

.container {
  max-width: 1120px;
  margin: auto;
  padding: 0 10px;
}

.header__wrapper {
  min-height: 70px;
  display: flex;
  align-items: center;
  justify-content: space-between;
}

@media only screen and (max-width: 768px) {
  .header__wrapper {
    flex-direction: column;
  }
}

.logo {
  display: flex;
  align-items: flex-end;
}

.logo_pic {
  font-size: 37px;
  line-height: 35px;
  margin-right: 8px;
}

.logo__text {
  text-transform: uppercase;
  font-size: 20px;
  color: #fff;
  font-family: AgencyFB, sans-serif;
}

.header__contacts {
  display: flex;
  color: #fff;
  align-items: center;
  font-family: OpenSans-S, sans-serif;
}

@media only screen and (max-width: 456px) {
  .header__contacts {
    flex-direction: column;
  }
}

.contacts__phone {
  font-size: 26px;
  margin-right: 38px;
  letter-spacing: 1px;
}

@media only screen and (max-width: 456px) {
  .contacts__phone {
    margin-right: 0;
  }
}

.contacts__info {
  font-size: 14px;
}

.contacts__pic {
  font-size: 28px;
  line-height: 28px;
  margin-right: 12px;
}

@media only screen and (max-width: 456px) {
  .contacts__pic {
    margin-right: 0;
  }
}

.intro {
  width: 100%;
  background-image: url("../img/penthouse-01.png");
  overflow: hidden;
  background-repeat: no-repeat;
  background-size: cover;
  color: #fff;
}

.intro__wrapper {
  display: flex;
  min-height: 100vh;
  justify-content: space-between;
  align-items: center;
}

@media only screen and (max-width: 768px) {
  .intro__wrapper {
    flex-direction: column;
    align-items: center;
    text-align: center;
  }
}

.name {
  font-family: AgencyFB, sans-serif;
  font-size: 64px;
  margin-bottom: 4px;
}

.name span {
  color: #F38638;
}

.about {
  font-size: 38px;
  text-transform: uppercase;
  letter-spacing: 1px;
  line-height: 45px;
  font-family: Raleway-R, sans-serif;
}

.intro__text {
  max-width: 440px;
}

@media only screen and (max-width: 768px) {
  .intro__text {
    margin-bottom: 30px;
  }
}

.intro__text hr {
  width: 64px;
  margin-left: 6px;
  margin-bottom: 35px;
}

@media only screen and (max-width: 768px) {
  .intro__text hr {
    margin: 25px auto;
  }
}

.calltoaction {
  font-family: Raleway-R, sans-serif;
  font-size: 20px;
  line-height: 23px;
  margin-bottom: 40px;
}

.calltoaction_btn {
  text-decoration: none;
  color: #fff;
  font-family: OpenSans-S, sans-serif;
  font-size: 15px;
  border: 2px solid #fff;
  padding: 10px 29px;
}

.calltoaction_btn:hover {
  color: #F38638;
  border: 2px solid #F38638;
}

.calltoaction_btn:hover .icon-arrow {
  fill: #F38638;
}

.icon-arrow {
  fill: #fff;
  font-size: 12px;
  margin-left: 5px;
}

.topform_wrapper {
  max-width: 360px;
  width: 360px;
  background-color: #F5F5F5;
  border-radius: 3px;
}

@media only screen and (max-width: 768px) {
  .topform_wrapper {
    margin-bottom: 30px;
  }
}

.topform__title {
  font-family: OpenSans-B, sans-serif;
  font-size: 18px;
  text-transform: uppercase;
  color: #333333;
  text-align: center;
  line-height: 55px;
}

.topform__title span {
  color: #F38638;
}

.topform {
  display: flex;
  flex-direction: column;
  background-color: #fff;
  padding-top: 25px;
  border-radius: 0 0 3px 3px;
}

.topform textarea {
  resize: none;
  border-top: 2px solid #FAFAFA;
  height: 80px;
}

.topform__item {
  line-height: 30px;
  padding-left: 10px;
  margin: 0 30px 30px 25px;
  font-family: OpenSans-R, sans-serif;
  border-bottom: 1px solid #D6D6D6;
  border-left: 1px solid #FAFAFA;
  border-right: 1px solid #FAFAFA;
}

.topform__item::placeholder {
  color: #BDBDBD;
}

.submit {
  margin: 0;
  height: 54px;
  border: none;
  background-color: #F38638;
  border-radius: 0 0 3px 3px;
  font-family: OpenSans-S, sans-serif;
  color: #fff;
  font-size: 16px;
  width: 100%;
  margin-bottom: -1px;
}

.awesome__wrapper {
  padding-top: 30px;
}

.title {
  width: 100%;
  text-align: center;
  margin-bottom: 45px;
}

.title hr {
  width: 64px;
  border: 1px solid #F38638;
  margin-top: 20px;
  margin-bottom: 27px;
}

.main_title {
  font-family: Raleway-R, sans-serif;
  color: #333333;
  font-size: 35px;
  text-transform: uppercase;
}

.secondary_title {
  font-family: OpenSans-R, sans-serif;
  color: #86878B;
  font-size: 14px;
}

.circles {
  width: 100%;
  display: flex;
  justify-content: space-between;
  margin-bottom: 110px;
}

@media only screen and (max-width: 768px) {
  .circles {
    flex-direction: column;
    align-items: center;
  }
}

.circles__wrapper {
  width: 183px;
  display: flex;
  flex-direction: column;
  align-items: center;
}

@media only screen and (max-width: 768px) {
  .circles__wrapper {
    margin-bottom: 40px;
  }
}

.circle {
  width: 100px;
  height: 100px;
  border: 2px solid #F38638;
  border-radius: 100px;
  display: flex;
  justify-content: center;
  align-items: center;
  color: #F38638;
  margin-bottom: 30px;
}

.circles__text {
  text-align: center;
}

.bold {
  font-family: OpenSans-B, sans-serif;
  font-size: 20px;
  color: #333333;
  line-height: 22px;
}

.regular {
  font-size: 14px;
  color: #333333;
  font-family: OpenSans-S, sans-serif;
}

.gallery {
  display: grid;
  grid-template-columns: repeat(auto-fill, minmax(380px, 1fr));
  grid-gap: 10px;
  margin-bottom: 110px;
}

@media only screen and (max-width: 420px) {
  .gallery {
    grid-template-columns: repeat(auto-fill, minmax(300px, 1fr));
  }
}

.gallery__item {
  height: 380px;
  display: flex;
  align-items: flex-end;
  justify-content: center;
  color: #fff;
  font-family: OpenSans-B, sans-serif;
  font-size: 30px;
  padding-bottom: 10px;
}

@media only screen and (max-width: 420px) {
  .gallery__item {
    font-size: 20px;
    padding-bottom: 20px;
  }
}

.gallery__picname {
  text-decoration: none;
  color: #fff;
}

.gallery__picname:hover {
  color: #f38638;
}

.home_sail {
  background-image: url("../img/gallerypic1.png");
  background-repeat: no-repeat;
  background-size: cover;
  overflow: hidden;
}

.ap_sail {
  background-image: url("../img/gallerypic2.png");
  background-repeat: no-repeat;
  background-size: cover;
  overflow: hidden;
}

.home_rent {
  background-image: url("../img/gallerypic3.png");
  background-repeat: no-repeat;
  background-size: cover;
  overflow: hidden;
}

.ap_rent {
  background-image: url("../img/gallerypic4.png");
  background-repeat: no-repeat;
  background-size: cover;
  overflow: hidden;
}

.footer {
  width: 100%;
  background-color: #000;
  height: 100px;
  padding-top: 40px;
  text-align: center;
}

.footer__social {
  display: flex;
  justify-content: center;
  font-size: 22px;
  color: #fff;
  fill: #fff;
  margin-bottom: 5px;
}

.footer__social a {
  text-decoration: none;
}

.icon-facebook {
  fill: #fff;
}

.icon-facebook:hover {
  fill: #F38638;
}

.icon-instagram {
  fill: #fff;
  margin: 0 25px;
}

.icon-instagram:hover {
  fill: #F38638;
}

.icon-share {
  fill: #fff;
}

.icon-share:hover {
  fill: #F38638;
}

.copyright {
  font-family: OpenSans-R, sans-serif;
  font-size: 12px;
  color: #878787;
}

.hidden {
  display: none;
}
